// app.js - Navegaci√≥n y Funciones B√°sicas
import { supabase, clientesAPI, productosAPI, ventasAPI, comprasAPI, reportesAPI } from './supabase.js'
import { cargarDashboard } from './dashboard.js';
import { cargarClientes, mostrarModalCliente, editarCliente, guardarCliente, eliminarCliente, filtrarClientes, limpiarFiltrosClientes } from './clientes.js';
import { cargarProductos, mostrarModalProducto, guardarProducto, editarProducto, eliminarProducto, filtrarProductos, limpiarFiltrosProductos } from './productos.js';
import { cargarVentas, mostrarModalVenta, agregarProductoVenta, removerProductoVenta, guardarVenta, filtrarVentas, limpiarFiltrosVentas, verDetalleVentaModal, eliminarVenta as eliminarVentaCompleta, editarProductoVenta, guardarEdicionProductoVenta } from './ventas.js';
import { cargarCompras, mostrarModalCompra, agregarProductoCompra, removerProductoCompra, guardarCompra, verDetalleCompra, eliminarCompra as eliminarCompraCompleta } from './compras.js';
import { cargarReportes } from './reportes.js';


// Estado global de la aplicaci√≥n
export const estadoApp = {
    seccionActual: 'dashboard',
    datos: {
        clientes: [],
        productos: [],
        ventas: [],
        compras: [],
        usuario: null // Para guardar la info del usuario logueado
    }
};

// Funci√≥n para mostrar una secci√≥n y ocultar las dem√°s
export function mostrarSeccion(seccion) {
    // Ocultar todas las secciones
    document.querySelectorAll('.seccion').forEach(sec => {
        sec.style.display = 'none';
    });
    
    // Remover clase active de todos los enlaces
    document.querySelectorAll('.sidebar .nav-link').forEach(link => {
        link.classList.remove('active');
    });
    
    // Mostrar la secci√≥n seleccionada
    const seccionElement = document.getElementById(seccion);
    if (seccionElement) {
        seccionElement.style.display = 'block';
    }
    
    // Activar el enlace correspondiente en el sidebar
    const enlaces = document.querySelectorAll('.sidebar .nav-link');
    enlaces.forEach(link => {
        if (link.textContent.includes(getTituloSeccion(seccion))) {
            link.classList.add('active');
        }
    });
    
    // Actualizar estado
    estadoApp.seccionActual = seccion;
    
    // Cargar datos espec√≠ficos de la secci√≥n
    cargarDatosSeccion(seccion);
}
window.mostrarSeccion = mostrarSeccion;

// Funci√≥n auxiliar para obtener el t√≠tulo de la secci√≥n
function getTituloSeccion(seccion) {
    const titulos = {
        'dashboard': 'Dashboard',
        'clientes': 'Clientes',
        'productos': 'Productos',
        'ventas': 'Ventas',
        'compras': 'Compras',
        'reportes': 'Reportes'
    };
    return titulos[seccion] || seccion;
}

// Funci√≥n para cargar datos seg√∫n la secci√≥n
async function cargarDatosSeccion(seccion) {
    console.log(`Cargando datos para: ${seccion}`);
    try {
        switch(seccion) {
            case 'dashboard':
                await cargarDashboard();
                break;
            case 'clientes':
                await cargarClientes();
                break;
            case 'productos':
                await cargarProductos();
                break;
            case 'ventas':
                await cargarVentas();
                break;
            case 'compras':
                await cargarCompras();
                break;
            case 'reportes':
                await cargarReportes();
                break;
        }
    } catch (error) {
        console.error(`Error cargando secci√≥n ${seccion}:`, error);
        mostrarAlerta(`Error al cargar ${seccion}: ${error.message}`, 'danger');
    }
}

// Funci√≥n para mostrar alertas
export function mostrarAlerta(mensaje, tipo = 'info') {
    const alertContainer = document.getElementById('alertContainer');
    
    const alerta = document.createElement('div');
    alerta.className = `alert alert-${tipo} alert-dismissible fade show`;
    alerta.innerHTML = `
        <i class="bi bi-${getIconoAlerta(tipo)}"></i>
        ${mensaje}
        <button type="button" class="btn-close" data-bs-dismiss="alert"></button>
    `;
    
    alertContainer.appendChild(alerta);
    
    // Auto-eliminar despu√©s de 5 segundos
    setTimeout(() => {
        if (alerta.parentNode) {
            alerta.remove();
        }
    }, 3000);
}

// Funci√≥n auxiliar para iconos de alerta
function getIconoAlerta(tipo) {
    const iconos = {
        'success': 'check-circle',
        'danger': 'exclamation-triangle',
        'warning': 'exclamation-circle',
        'info': 'info-circle'
    };
    return iconos[tipo] || 'info-circle';
}

// Funci√≥n para formatear fechas
export function formatearFecha(fechaString) {
    const opciones = { 
        year: 'numeric', 
        month: '2-digit', 
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit'
    };
    return new Date(fechaString).toLocaleDateString('es-ES', opciones);
}

// Funci√≥n para formatear moneda
export function formatearMoneda(monto) {
    return new Intl.NumberFormat('es-MX', {
        style: 'currency',
        currency: 'MXN' // Ver si esta bien MXN
    }).format(monto);
}

// Inicializar la aplicaci√≥n cuando se carga la p√°gina
document.addEventListener('DOMContentLoaded', function() {
    // Configurar la l√≥gica del men√∫ de hamburguesa para m√≥viles, independientemente del login.
    configurarMenuHamburguesa();

    // Comprobar el estado de autenticaci√≥n
    supabase.auth.onAuthStateChange(async (event, session) => {
        if (session) {
            // Usuario est√° logueado
            console.log('Usuario autenticado:', session.user);
            estadoApp.datos.usuario = session.user;

            // Si estamos en la p√°gina de login/signup, redirigir a la app principal
            if (window.location.pathname.includes('login.html') || window.location.pathname.includes('signup.html')) {
                window.location.href = '/index.html';
            } else {
                // Estamos en la app principal, cargar todo
                inicializarApp();
            }
        } else {
            // Usuario no est√° logueado
            console.log('Usuario no autenticado.');
            estadoApp.datos.usuario = null;
            // Si no estamos en login/signup, redirigir a login
            if (!window.location.pathname.includes('login.html') && !window.location.pathname.includes('signup.html')) {
                window.location.href = '/login.html';
            }
        }
    });
});


// Funci√≥n que se ejecuta una vez que el usuario est√° autenticado
function inicializarApp() {
    console.log('Inicializando aplicaci√≥n...');
    
    // Configurar el bot√≥n de logout
    const logoutButton = document.getElementById('logout-button');
    if (logoutButton) {
        logoutButton.addEventListener('click', async () => {
            // Limpiar el estado de la aplicaci√≥n antes de cerrar sesi√≥n
            limpiarEstadoApp();
            const { error } = await supabase.auth.signOut();
            if (error) {
                mostrarAlerta('Error al cerrar sesi√≥n: ' + error.message, 'danger');
            }
            // onAuthStateChange se encargar√° de la redirecci√≥n a login.html
            // y de limpiar la interfaz.
        });
    }

    // Al iniciar sesi√≥n, limpiar cualquier dato residual y mostrar el dashboard.
    // El dashboard cargar√° sus propios datos necesarios.
    limpiarEstadoApp();
    mostrarSeccion('dashboard');
}

// Funci√≥n para manejar el men√∫ en dispositivos m√≥viles
function configurarMenuHamburguesa() {
    const toggler = document.querySelector('.navbar-toggler');
    const sidebar = document.querySelector('.sidebar');
    const overlay = document.querySelector('.sidebar-overlay');

    if (toggler && sidebar && overlay) {
        toggler.addEventListener('click', () => {
            const isOpening = !sidebar.classList.contains('show');

            sidebar.classList.toggle('show');
            overlay.classList.toggle('show');

            // Ocultar/mostrar el bot√≥n hamburguesa
            if (isOpening) {
                toggler.style.display = 'none'; // Ocultar al abrir
            } else {
                toggler.style.display = 'block'; // Mostrar al cerrar
            }
        });

        overlay.addEventListener('click', () => {
            sidebar.classList.remove('show');
            overlay.classList.remove('show');
            toggler.style.display = 'block';
        });

        // Ocultar men√∫ al hacer clic en una opci√≥n
        sidebar.addEventListener('click', (e) => {
            if (e.target.classList.contains('nav-link')) {
                sidebar.classList.remove('show');
                overlay.classList.remove('show');
                toggler.style.display = 'block';
            }
        });
    }
}
// Nueva funci√≥n para limpiar el estado de la aplicaci√≥n
function limpiarEstadoApp() {
    console.log('Limpiando estado de la aplicaci√≥n...');
    estadoApp.datos.clientes = [];
    estadoApp.datos.productos = [];
    estadoApp.datos.ventas = [];
    estadoApp.datos.compras = [];

    // Limpiar las tablas en la UI para que no se muestren datos viejos
    document.getElementById('tablaClientes').innerHTML = '';
    document.getElementById('tablaProductos').innerHTML = '';
    document.getElementById('tablaVentas').innerHTML = '';
    document.getElementById('tablaCompras').innerHTML = '';
    // Limpiar tambi√©n el contenido del dashboard para evitar mostrar datos viejos
    document.getElementById('topClientesContainer').innerHTML = '';
    const chartCanvas = document.getElementById('chartProductosPopulares');
    if (chartCanvas && chartCanvas.chart) chartCanvas.chart.destroy();
}

export function actualizarEstadisticas() {
    try {
        const hoy = new Date().toISOString().split('T')[0];
        const inicioMes = new Date();
        inicioMes.setDate(1);
        const inicioMesStr = inicioMes.toISOString().split('T')[0];
        
        // Calcular ventas de hoy
        const ventasHoy = estadoApp.datos.ventas
            .filter(v => v.fecha_venta && v.fecha_venta.startsWith(hoy))
            .reduce((sum, v) => sum + (v.total || 0), 0);
        
        // Calcular ingresos del mes
        const ingresosMes = estadoApp.datos.ventas
            .filter(v => v.fecha_venta && v.fecha_venta >= inicioMesStr)
            .reduce((sum, v) => sum + (v.total || 0), 0);
        
        // Actualizar elementos del DOM
        const ventasHoyElement = document.getElementById('ventasHoy');
        const ingresosMesElement = document.getElementById('ingresosMes');
        const totalClientesElement = document.getElementById('totalClientes');
        const totalProductosElement = document.getElementById('totalProductos');
        
        if (ventasHoyElement) ventasHoyElement.textContent = formatearMoneda(ventasHoy);
        if (ingresosMesElement) ingresosMesElement.textContent = formatearMoneda(ingresosMes);
        if (totalClientesElement) totalClientesElement.textContent = estadoApp.datos.clientes.length;
        if (totalProductosElement) totalProductosElement.textContent = estadoApp.datos.productos.length;
        
    } catch (error) {
        console.error('Error actualizando estad√≠sticas:', error);
    }
}

